.about-info {
  position: absolute;
  left: 10%;
  top: 40%;
  width: 500px;
  height: 300px;
  display: flex;
  flex-direction: column;
}
@media (max-width: 1024px) {
  .about-info {
    top: 30%;
    width: 400px;
  }
}
.about-info .about-info__title {
  color: var(--black);
  letter-spacing: 0.05em;
  font-size: 65px;
  margin-bottom: 10px;
}
.about-info .about-info__text {
  margin-bottom: 100px;
}
.about-info .about-info__text .about-info__text-item {
  color: var(--black);
  letter-spacing: 0.05em;
  font-size: 25px;
}
.about-info .about-info__buttons {
  display: flex;
  gap: 10px;
}

