.contact__form {
  position: absolute;
  top: 10%;
  left: 10%;
  width: 500px;
  height: 600px;
  border: 2px solid var(--white);
  border-radius: 5px;
  padding: 50px 0;
  box-shadow: 7px 7px 6px rgba(0, 0, 0, 0.3), inset 7px 7px 6px rgba(0, 0, 0, 0.3);
}
@media (max-width: 1024px) {
  .contact__form {
    width: 400px;
    height: 500px;
  }
}
.contact__form .contact__form-body {
  margin-bottom: 50px;
}
@media (max-width: 1024px) {
  .contact__form .contact__form-body {
    margin-bottom: 40px;
  }
}
.contact__form .contact__form-button {
  display: flex;
  max-width: 500px;
  justify-content: center;
}

