.info-section-two{
  position: absolute;
  left: 10%;
  top: 40%;
  width: 340px;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: 1024px) {
    top: 30%;
  }
  @media (max-width: 768px) {
    width: 300px;
  }
  .info-section-two__title{
    color: var(--white);
    letter-spacing: calc(1em/5/4);
    text-align: center;
    font-size: 45px;
    margin-bottom: 10px;
    @media (max-width: 768px) {
      font-size: 40px;
    }
  }
  .info-section-two__text{
    margin-bottom: 100px;
    font-size: 20px;
    .info-section-two__text-item{
      color: var(--white);
      letter-spacing: calc(1em/5/4);
      line-height: 144%;
      text-align: center;
    }
  }
  .info-section-two__button{
    width: 370px;
    background-color: var(--white);
    border-radius: 50px;
    .info-section-two__button-item{
      font-size: 40px;
      letter-spacing: calc(1em/5);
      text-transform: uppercase;
      color: var(--black);
      padding: 5px 10px;
      transition: all .5s;
      &:hover{
        animation: rr .5s linear;
      }
    }
  }
}

@keyframes rr {
  0%{
    letter-spacing: calc(1em/5);
  }
  50%{
    letter-spacing: calc(1em/4);
  }
  100%{
    letter-spacing: calc(1em/5);
  }
}