.info-section-two {
  position: absolute;
  left: 10%;
  top: 40%;
  width: 340px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 1024px) {
  .info-section-two {
    top: 30%;
  }
}
@media (max-width: 768px) {
  .info-section-two {
    width: 300px;
  }
}
.info-section-two .info-section-two__title {
  color: var(--white);
  letter-spacing: 0.05em;
  text-align: center;
  font-size: 45px;
  margin-bottom: 10px;
}
@media (max-width: 768px) {
  .info-section-two .info-section-two__title {
    font-size: 40px;
  }
}
.info-section-two .info-section-two__text {
  margin-bottom: 100px;
  font-size: 20px;
}
.info-section-two .info-section-two__text .info-section-two__text-item {
  color: var(--white);
  letter-spacing: 0.05em;
  line-height: 144%;
  text-align: center;
}
.info-section-two .info-section-two__button {
  width: 370px;
  background-color: var(--white);
  border-radius: 50px;
}
.info-section-two .info-section-two__button .info-section-two__button-item {
  font-size: 40px;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  color: var(--black);
  padding: 5px 10px;
  transition: all 0.5s;
}
.info-section-two .info-section-two__button .info-section-two__button-item:hover {
  animation: rr 0.5s linear;
}

@keyframes rr {
  0% {
    letter-spacing: 0.2em;
  }
  50% {
    letter-spacing: 0.25em;
  }
  100% {
    letter-spacing: 0.2em;
  }
}

