.decor{
  top: 20%;
  right: 1%;
  position: absolute;
  width: 15px;
  margin: 0;
  @media (max-width: 1024px) {
    top: 30%;
    right: 1%;
  }
  .decor-text{
    text-transform: uppercase;
    font-size: 20px;
    letter-spacing: calc(1em/5/2);
    writing-mode: vertical-lr;
    &:before, &:after{
      content: "";
      height: 60px;
      width: 2px;
      position: absolute;
      background-color: var(--black);
      left: 30%;
    }
    &:before{
      top: -70px;
    }
    &:after{
      bottom: -70px;
    }
  }
}