.button{
  width: 370px;
  border-radius: 50px;
  .button__item{
    font-size: 40px;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    padding: 5px 10px;
    transition: all 0.5s;
    &:hover{
      animation: animationButton 0.5s linear;
    }
  }
}

@keyframes animationButton {
  0% {
    letter-spacing: 0.2em;
  }
  50% {
    letter-spacing: 0.25em;
  }
  100% {
    letter-spacing: 0.2em;
  }
}