.network-item{
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
  .network-item__img{
    position: absolute;
    width: 60px;
    height: 60px;
    &:after{
      content: "";
      position: absolute;
      left: 110%;
      top: 50%;
      width: 90px;
      height: 1px;
      background-color: var(--white);
      @media (max-width: 1024px) {
        width: 65px;
      }
    }
    .network-item__img-item{
      position: absolute;
      width: 60px;
      height: 60px;
    }
  }
  .network-item__text{
    font-size: 24px;
    padding-left: 170px;
    color: var(--white);
    letter-spacing: calc(1em/5/4);
    transition: all .3s;
    &:hover{
      letter-spacing: calc(1em/5/3);
    }
    @media (max-width: 1024px) {
      padding-left: 140px;
    }
  }
}