.decor-arrow__white {
  position: absolute;
  bottom: 100px;
  right: 1%;
  width: 3px;
}
@media (max-width: 1024px) {
  .decor-arrow__white {
    right: 1.5%;
  }
}
.decor-arrow__white .decor-arrow-item__white {
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 60px;
  cursor: pointer;
}
.decor-arrow__white .decor-arrow-item__white div {
  height: 60px;
  width: 3px;
  position: relative;
  display: block;
  background: linear-gradient(to top, var(--gray), var(--white));
  top: -5px;
}
.decor-arrow__white .decor-arrow-item__white:before, .decor-arrow__white .decor-arrow-item__white:after {
  content: "";
  height: 20px;
  width: 3px;
  position: absolute;
  display: block;
  bottom: 0;
}
.decor-arrow__white .decor-arrow-item__white:before {
  left: 7px;
  transform: rotate(45deg);
  background: linear-gradient(to top, var(--gray), var(--white));
}
.decor-arrow__white .decor-arrow-item__white:after {
  left: -7px;
  transform: rotate(-45deg);
  background: linear-gradient(to top, var(--gray), var(--white));
}

