.info-section-three{
  position: absolute;
  top: 20%;
  left: 10%;
  display: flex;
  flex-direction: column;
  width: 500px;
  height: 300px;
  .info-section-three__title{
    font-size: 50px;
    margin-bottom: 10px;
  }
  .info-section-three__text{
    .info-section-three__text-item{
      font-size: 24px;
    }
  }
  .info-section-three__images{
    position: absolute;
    top: 60%;
    left: 0;
    .info-section-three__images-item{
      position: absolute;
      width: 250px;
      height: 250px;
      background-image: url("../../../../assets/Group.png");
      background-size: auto 250px;
      background-repeat: no-repeat;
    }
  }
}