.network-item {
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
}
.network-item .network-item__img {
  position: absolute;
  width: 60px;
  height: 60px;
}
.network-item .network-item__img:after {
  content: "";
  position: absolute;
  left: 110%;
  top: 50%;
  width: 90px;
  height: 1px;
  background-color: var(--white);
}
@media (max-width: 1024px) {
  .network-item .network-item__img:after {
    width: 65px;
  }
}
.network-item .network-item__img .network-item__img-item {
  position: absolute;
  width: 60px;
  height: 60px;
}
.network-item .network-item__text {
  font-size: 24px;
  padding-left: 170px;
  color: var(--white);
  letter-spacing: 0.05em;
  transition: all 0.3s;
}
.network-item .network-item__text:hover {
  letter-spacing: 0.0666666667em;
}
@media (max-width: 1024px) {
  .network-item .network-item__text {
    padding-left: 140px;
  }
}

