.info-home{
  position: absolute;
  left: 65%;
  top: 40%;
  font-family: var(--ff);
  width: 340px;
  display: flex;
  flex-direction: column;
  align-items: center;
  @media (max-width: 1024px){
    left: 60%;
  }
  @media (max-width: 768px) {
    width: 300px;
    left: 45%;
  }
  .info-home__title{
    position: relative;
    text-align: center;
    letter-spacing: calc(1em/10);
    font-size: 45px;
    font-weight: bold;
    margin-bottom: 10px;
    @media (max-width: 768px) {
      font-size: 40px;
    }
    &:before{
      content: "";
      height: 2px;
      width: 100%;
      position: absolute;
      top: 50%;
      left: 0;
      background-color: var(--black);
    }
  }
  .info-home__text{
    font-size: 20px;
    margin-bottom: 60px;
    .info-home__text-item{
      line-height: 144%;
      letter-spacing: calc(1em/5/4);
      text-align: center;
    }
  }
}
