.section-two__images {
  position: absolute;
  left: 50%;
  top: 20%;
}
@media (max-width: 1024px) {
  .section-two__images {
    top: 10%;
  }
}
.section-two__images .section-two__images-item {
  display: block;
  position: absolute;
  width: 602px;
  height: 624px;
  background-image: url("../../../../assets/Geners.png");
  background-repeat: no-repeat;
}
@media (max-width: 1024px) {
  .section-two__images .section-two__images-item {
    width: 490px;
    height: 500px;
    background-size: auto 500px;
  }
}
@media (max-width: 768px) {
  .section-two__images .section-two__images-item {
    width: 340px;
    height: 350px;
    background-size: auto 350px;
  }
}

