.back-images{
  position: absolute;
  top: 0;
  left: 0;
  .facelessMan{
    display: block;
    position: absolute;
    top: 80px;
    left: 120px;
    width: 447px;
    height: 889px;
    background-image: url("../../../../assets/FacelessMan1.png");
    background-repeat: no-repeat;
    background-size: auto 700px;
    @media (max-width: 1024px) {
      width: 320px;
      height: 500px;
      background-size: auto 500px;
    }
    @media (max-width: 768px) {
      left: 100px;
      width: 225px;
      height: 450px;
      background-size: auto 450px;
    }
  }
  .tattoo{
    display: block;
    position: absolute;
    top: 200px;
    width: 1054px;
    height: 179px;
    background-image: url("../../../../assets/TATTOO.png");
    background-repeat: no-repeat;
    @media (max-width: 1024px) {
      width: 600px;
      height: 110px;
      background-size: 600px auto;
    }
    @media (max-width: 768px) {
      top: 150px;
      width: 500px;
      height: 200px;
      background-size: 500px auto;
    }
  }
}