.footer__links {
  position: relative;
  width: 700px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1024px) {
  .footer__links {
    height: 280px;
  }
}
.footer__links div {
  cursor: pointer;
}
.footer__links .footer__links-facebook {
  width: 60px;
  height: 60px;
  background-image: url("../../../../assets/typcn_social-facebook-circular.png");
}
.footer__links .footer__links-instagram {
  width: 60px;
  height: 60px;
  background-image: url("../../../../assets/typcn_social-instagram-circular.png");
}
.footer__links .footer__links-twitter {
  width: 60px;
  height: 60px;
  background-image: url("../../../../assets/typcn_social-twitter-circular.png");
}
.footer__links .footer__links-youtube {
  width: 60px;
  height: 60px;
  background-image: url("../../../../assets/typcn_social-youtube-circular.png");
}
.footer__links .footer__links-telegram {
  width: 60px;
  height: 60px;
  background-image: url("../../../../assets/typcn_social-telegram-circular.png");
  background-size: auto 50px;
  background-repeat: no-repeat;
  background-position-y: 3px;
  background-position-x: 5px;
}

