.contact__form-item {
  display: flex;
  gap: 20%;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
  @media (max-width: 1024px) {
    gap: 10%;
  }
  &:nth-last-child(1){
    align-items: normal;
  }
  .contact__form-item__title {
    width: 120px;
    letter-spacing: calc(1em/5/2);
    color: var(--white);
  }
  .contact__form-item__input{
    display: block;
    width: 200px;
    border-radius: 5px;
    padding: 5px;
    box-shadow: 3px 3px 3px rgb(0, 0, 0, 0.3);
    font-size: 20px;
  }
}