.navbar {
  position: fixed;
  width: 100%;
  max-width: 1440px;
  background-color: var(--gray);
  box-shadow: 0 7px 6px rgb(0, 0, 0, 0.3);
  z-index: 100;
  @media (max-width: 1024px) {
    max-width: 1024px;
  }
  @media (max-width: 768px) {
    max-width: 768px;
    height: 10vh;
  }
  .navbar__container {
    display: flex;
    justify-content: space-between;
    padding: 5px 20px;

    .navbar__logo {
      width: 123px;
      height: 27px;

      & div {
        display: block;
        position: relative;
        top: 5px;
        width: 100%;
        height: 100%;
        background-image: url("../../assets/LOGO.png");
        background-repeat: no-repeat;
      }
    }

    .navbar__menu {
      display: flex;
      gap: 48px;
      @media (max-width: 768px) {
        position: fixed;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        left: -100%;
        width: 100%;
        height: 100vh;
        top: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0, 0, 0, 0.6);
        transition: left .4s;
        &.active{
          left: 0;
        }
      }
      & li {
        font-size: 37px;
        letter-spacing: calc(1em / 5);
        text-align: center;
        font-family: var(--ff);
        @media (max-width: 768px) {
          font-size: 30px;
        }

        .navbar__menu-link {
          color: var(--white);
          transition: all .3s;
          z-index: 5;

          &:hover {
            color: var(--red)
          }

          &.active {
            color: var(--red)
          }
        }
      }
    }

    .navbar__menu-burger {
      display: none;
      @media (max-width: 768px) {
        position: relative;
        width: 30px;
        height: 36px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
      & span {
        position: absolute;
        width: 30px;
        height: 2px;
        background-color: var(--white);
        &:before, &:after{
          content: "";
          width: 30px;
          height: 2px;
          position: absolute;
          background-color: var(--white);
        }
        &:before{
          top: -7px;
        }
        &:after{
          bottom: -7px;
        }
      }
    }
  }
}