.navbar {
  position: fixed;
  width: 100%;
  max-width: 1440px;
  background-color: var(--gray);
  box-shadow: 0 7px 6px rgba(0, 0, 0, 0.3);
  z-index: 100;
}
@media (max-width: 1024px) {
  .navbar {
    max-width: 1024px;
  }
}
@media (max-width: 768px) {
  .navbar {
    max-width: 768px;
    height: 10vh;
  }
}
.navbar .navbar__container {
  display: flex;
  justify-content: space-between;
  padding: 5px 20px;
}
.navbar .navbar__container .navbar__logo {
  width: 123px;
  height: 27px;
}
.navbar .navbar__container .navbar__logo div {
  display: block;
  position: relative;
  top: 5px;
  width: 100%;
  height: 100%;
  background-image: url("../../assets/LOGO.png");
  background-repeat: no-repeat;
}
.navbar .navbar__container .navbar__menu {
  display: flex;
  gap: 48px;
}
@media (max-width: 768px) {
  .navbar .navbar__container .navbar__menu {
    position: fixed;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    left: -100%;
    width: 100%;
    height: 100vh;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.6);
    transition: left 0.4s;
  }
  .navbar .navbar__container .navbar__menu.active {
    left: 0;
  }
}
.navbar .navbar__container .navbar__menu li {
  font-size: 37px;
  letter-spacing: 0.2em;
  text-align: center;
  font-family: var(--ff);
}
@media (max-width: 768px) {
  .navbar .navbar__container .navbar__menu li {
    font-size: 30px;
  }
}
.navbar .navbar__container .navbar__menu li .navbar__menu-link {
  color: var(--white);
  transition: all 0.3s;
  z-index: 5;
}
.navbar .navbar__container .navbar__menu li .navbar__menu-link:hover {
  color: var(--red);
}
.navbar .navbar__container .navbar__menu li .navbar__menu-link.active {
  color: var(--red);
}
.navbar .navbar__container .navbar__menu-burger {
  display: none;
}
@media (max-width: 768px) {
  .navbar .navbar__container .navbar__menu-burger {
    position: relative;
    width: 30px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.navbar .navbar__container .navbar__menu-burger span {
  position: absolute;
  width: 30px;
  height: 2px;
  background-color: var(--white);
}
.navbar .navbar__container .navbar__menu-burger span:before, .navbar .navbar__container .navbar__menu-burger span:after {
  content: "";
  width: 30px;
  height: 2px;
  position: absolute;
  background-color: var(--white);
}
.navbar .navbar__container .navbar__menu-burger span:before {
  top: -7px;
}
.navbar .navbar__container .navbar__menu-burger span:after {
  bottom: -7px;
}

