.networks {
  position: absolute;
  top: 15%;
  left: 45%;
  background-color: var(--gray);
  width: 500px;
  height: 550px;
  box-shadow: 7px 7px 6px rgba(0, 0, 0, 0.3);
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1024px) {
  .networks {
    width: 400px;
    height: 450px;
    left: 48%;
  }
}
.networks .networks__body {
  width: 400px;
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
}

