.decor-arrow__red {
  position: absolute;
  bottom: 100px;
  right: 1%;
  width: 3px;
}
@media (max-width: 1024px) {
  .decor-arrow__red {
    right: 1.5%;
  }
}
.decor-arrow__red .decor-arrow-item__red {
  position: absolute;
  top: 0;
  left: 0;
  width: 3px;
  height: 60px;
  cursor: pointer;
}
.decor-arrow__red .decor-arrow-item__red div {
  height: 60px;
  width: 3px;
  position: relative;
  display: block;
  background: linear-gradient(to top, var(--red), var(--gray));
  top: -5px;
}
.decor-arrow__red .decor-arrow-item__red:before, .decor-arrow__red .decor-arrow-item__red:after {
  content: "";
  height: 20px;
  width: 3px;
  position: absolute;
  display: block;
  bottom: 0;
}
.decor-arrow__red .decor-arrow-item__red:before {
  left: 7px;
  transform: rotate(45deg);
  background: linear-gradient(to top, var(--red), var(--gray));
}
.decor-arrow__red .decor-arrow-item__red:after {
  left: -7px;
  transform: rotate(-45deg);
  background: linear-gradient(to top, var(--red), var(--gray));
}

