.decor {
  top: 20%;
  right: 1%;
  position: absolute;
  width: 15px;
  margin: 0;
}
@media (max-width: 1024px) {
  .decor {
    top: 30%;
    right: 1%;
  }
}
.decor .decor-text {
  text-transform: uppercase;
  font-size: 20px;
  letter-spacing: 0.1em;
  writing-mode: vertical-lr;
}
.decor .decor-text:before, .decor .decor-text:after {
  content: "";
  height: 60px;
  width: 2px;
  position: absolute;
  background-color: var(--black);
  left: 30%;
}
.decor .decor-text:before {
  top: -70px;
}
.decor .decor-text:after {
  bottom: -70px;
}

