.designs{
  position: relative;
  top: 40px;
  min-height: 100vh;
  background: linear-gradient(to bottom, var(--black), var(--red));
  .designs__img{
    width: 100%;
    height: 70vh;
    background-image: url("../../assets/1.png");
    background-repeat: no-repeat;
    background-size: cover;
    @media (max-width: 1024px) {
      background-size: 1024px auto;
      width: 1024px;
      height: 60vh;
    }
    @media (max-width: 768px){
      background-size: 768px auto;
      width: 768px;
    }
    .designs__img-item{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 70vh;
      overflow: hidden;
      background-color: rgba(0, 0, 0, 0.8);
      @media (max-width: 1024px) {
        height: 60vh;
      }
    }
  }
  .designs__container{
    margin-top: 80px;
    padding: 0 100px 80px;
    position: relative;
    width: 100%;
    height: 100%;
    font-family: var(--ff);
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    @media (max-width: 768px) {
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    .designs__card{
      display: flex;
      flex-direction: column;
      width: 300px;
      height: 520px;
      gap: 10px;
      .designs__card-title{
        display: flex;
        justify-content: center;
        font-size: 30px;
        letter-spacing: calc(1em/5/3);
        transition: all .3s;
        & a{
          color: var(--white);
        }
        &:hover{
          letter-spacing: calc(1em/5/2);
        }
      }
      .designs__card-img{
        border: 2px solid black;
        border-radius: 5px;
        box-shadow: inset 7px 7px 6px rgb(0, 0, 0, 0.3),
        7px 7px 6px rgb(0, 0, 0, 0.3);
        .designs__card-img-item{
          width: 300px;
          height: 440px;
          display: flex;
          justify-content: center;
        }
      }
    }
  }
}