.contact__form-item {
  display: flex;
  gap: 20%;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}
@media (max-width: 1024px) {
  .contact__form-item {
    gap: 10%;
  }
}
.contact__form-item:nth-last-child(1) {
  align-items: normal;
}
.contact__form-item .contact__form-item__title {
  width: 120px;
  letter-spacing: 0.1em;
  color: var(--white);
}
.contact__form-item .contact__form-item__input {
  display: block;
  width: 200px;
  border-radius: 5px;
  padding: 5px;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.3);
  font-size: 20px;
}

